.maincontainer {
  height: 100vh;
  position: relative;
  position: fixed;
}
.maincontainer img {
  object-fit: cover;
  object-position: 55% 90%;
  width: 100vw;
  height: 100vh;
  /*width: 1530px;
  height: 740px;*/
}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Roboto Slab", serif;
}

a {
  list-style-type: none;
  text-decoration: none;
  color: black;
}

.container {
  box-sizing: border-box;
  /* width: 570px;*/
  width: 37vw;

  /* height: 100vh;
  overflow: auto;*/
  position: absolute;
  top: 0;
}

.nav {
  display: flex;
  width: 37vw;
  height: 75px;
  position: sticky;

  align-items: center;
  background-color: rgb(26, 150, 155);
  justify-content: space-between;
  padding: 10px;
}
.bell {
  font-size: smaller;
  color: rgb(69, 59, 59);
}
.bars {
  font-size: smaller;
  color: rgb(84, 84, 136);
}

.nav h1 {
  color: rgb(35, 37, 39);
  font-size: 25px;
}

.main {
  background-color: rgb(240, 240, 240);
  padding-top: 5px;
  width: 37vw;
  height: 100vh;
  overflow: auto;
}

.title {
  display: flex;
  color: rgb(35, 37, 39);
  background-color: rgb(186, 210, 224);
  padding: 10px;

  height: 55px;
  align-items: center;
  margin: 10px;
  justify-content: space-evenly;
  border-radius: 25px;
  font-size: 17px;
  cursor: pointer;
  overflow: hidden;
}

.slider {
  position: sticky;
  top: -9px;
}

.slider .first img {
  margin-left: auto;
  margin-right: auto;
  width: 50%;
  width: 37vw;
  /* width: 540px;*/
  height: 25vh;
}
.content {
  display: flex;
  justify-content: space-between;
  background-color: rgb(202, 207, 209);
  margin: 8px;
  font-size: 20px;
  border-radius: 5px;
}

.content .left h1 {
  color: rgb(20, 20, 20);
}

.left,
.right {
  flex-basis: 32%;
  font-size: 15px;
}

.right {
  padding-left: 35px;
}

.left img,
.right img {
  border-radius: 50%;
  padding: 5px;
  width: 95px;
  height: 50px;
  object-fit: cover;
  object-position: 10%;
  margin: 5px;
}

.middle {
  color: rgb(28, 153, 226);
  font-size: 24px;
  justify-content: center;
  align-items: center;
  padding-top: 25px;
  text-align: center;
  flex-basis: 34%;
}
.prize {
  color: black;
  padding-left: 30px;
  font-size: 15px;
}

.team {
  padding: 15px;
}

.footer {
  width: 100%;
  font-size: large;
  width: 37vw;
  height: 55px;
  align-items: center;
  border-radius: 0 0 5px 5px;
  display: flex;
  justify-content: space-between;
  padding: 5px;
  position: sticky;
  bottom: 0;
  text-align: center;
  background-color: rgb(9, 152, 157);
  /* background-color: rgb(122, 221, 228);
 /* background-color: rgb(210, 224, 230);*/
}
.house {
  font-size: smaller;
  color: rgb(18, 19, 19);
}
.walt {
  font-size: smaller;
  color: rgb(16, 16, 16);
}
.star {
  font-size: smaller;
  color: rgb(83, 85, 85);
}
.ellipsis {
  font-size: smaller;
  color: rgb(42, 38, 38);
}

.footer p {
  color: rgb(1, 10, 19);
  font-weight: bolder;
  text-align: center;
  font-size: smaller;
}

@media only screen and (max-width: 750px) {
  .container {
    margin-left: 65px;
    width: 80%;
    height: 100vh;
  }
  .main {
    width: 100%;
    height: 100vh;
  }
  .nav {
    width: 100%;
    height: 65px;
    justify-content: space-around;
  }
  .title {
    margin-left: auto;
    width: 90%;
    height: 45px;
    align-items: center;

    justify-content: space-evenly;

    margin-right: 30px;
  }
  .footer {
    width: 100%;
    justify-content: space-around;
    height: 50px;
    align-items: center;
  }

  .slider .first img {
    width: 100%;
    margin-left: auto;
    margin-right: auto;
  }
  .title {
    font-size: 13px;
    background-color: rgb(213, 233, 233);
  }
}

@media only screen and (max-width: 550px) {
  .container {
    width: 100vw;
    height: 100vh;
  }
  .main {
    width: 100%;
    height: 100vh;
  }
  .nav {
    width: 100%;
    height: 65px;
    justify-content: space-around;
  }
  .title {
    margin-left: auto;
    width: 90%;
    height: 45px;
    align-items: center;

    justify-content: space-evenly;

    margin-right: 30px;
  }
  .footer {
    width: 100%;
    justify-content: space-around;
    height: 50px;
    align-items: center;
  }

  .slider .first img {
    width: 100%;
    margin-left: auto;
    margin-right: auto;
  }
  .title {
    font-size: 13px;
    background-color: rgb(213, 233, 233);
  }
}

/* create.html  */

.live-matches {
  text-align: center;
  margin: auto;
  color: white;
  justify-content: center;
}

.head-main {
  display: flex;
  justify-content: space-between;
  background-color: antiquewhite;
  padding: 15px;
  margin: 5px;
}

.contest-content {
  margin: auto;
  padding: 2px;
  /*  text-align: center;*/
  width: 97%;
  background-color: rgb(172, 161, 161);
  border-radius: 10px 10px 0px 0px;
}

.row {
  display: flex;
  justify-content: space-between;
  border-radius: 5px 5px 0px 0px;
  /*  border-radius: 5px;*/
  background-color: azure;
  padding: 10px;
  margin: 5px;
  margin-bottom: 0;
  color: rgba(32, 32, 32, 0.383);
}

.pool-prize {
  margin: 12px;
  color: darkcyan;
}

.total-entry {
  display: inline;
}

.btn-prize {
  color: white;
  background-color: rgb(38, 95, 160);
  margin: 12px;
  margin-left: 0px;
  padding: 10px 15px 10px 15px;
  border: none;
  text-align: center;
}

.last-row {
  background-color: rgb(209, 236, 236);
  padding: 5px;
  margin: 5px;
  margin-top: 0;
  margin-bottom: 10px;
}

/*   join.css  */

.join-toggle {
  border-radius: 10px;
}

.join-box {
  display: flex;
  color: rgb(63, 133, 129);
  background-color: azure;
  /* background-color: white;*/
  margin: 8px;
  /*text-align: center;*/
  border-radius: 5px;
  justify-content: space-between;
  padding: 15px 35px;
}

.leaderboard {
  flex-basis: 10%;
  cursor: pointer;
}

.prize-breakup {
  cursor: pointer;
}

.leaderboard-list {
  margin: 5px;
}

.teams {
  background-color: antiquewhite;
  border-radius: 2px;
  padding: 5px;
  justify-content: space-between;
}

.ranks-prize {
  display: flex;
  padding: 5px;
  justify-content: space-between;
}

.leaderboard-team {
  background-color: azure;
  border-radius: 2px;

  display: flex;
  justify-content: flex-start;
  border-bottom: 1px solid rgb(9, 9, 22);
}

/* .team-list{
    border-radius: 10px;
    margin: 5px;
    background-color: antiquewhite;
}*/

.team-profile img {
  width: 50px;
  height: 50px;
  margin: 5px;
}
.leaderboard-team-name {
  margin: 20px;

  align-items: center;
}

.rankprize {
  display: flex;
  background-color: azure;
  border-radius: 2px;
  justify-content: space-between;
}
.ranklist,
.prizelist {
  border-bottom: 1px solid rgb(14, 25, 29);
  margin: 5px;
}

/*    create-team   */

.create-team-logo {
  display: flex;
  justify-content: space-between;
  margin: 5px;
  padding: 5px;
  background-color: azure;
}

.create-left-team-logo-img img,
.create-right-team-logo-img img {
  width: 50px;
  height: 40px;
  border-radius: 50%;
}

.create-total-player-taken p,
.create-credit-left p {
  color: rgba(32, 32, 32, 0.383);
}

.create-right-team-name,
.create-left-team-name {
  color: rgba(34, 33, 33, 0.383);
}

.create-no-of-bar-player-selected {
  display: flex;
  justify-content: space-between;
}

.create-bar {
  width: 100%;
  height: 1vh;
  background-color: rgb(66, 96, 129);
}

.player-collection {
  display: flex;
  justify-content: space-around;
  color: rgba(34, 33, 33, 0.383);
  padding: 10px;
}

.line-bar {
  width: 100%;
  background-color: rgba(115, 151, 160, 0.383);
  height: 2px;
}

.all-info-team-player {
  display: flex;
  justify-content: space-between;
  margin: 5px;
}
